import React from 'react';
import {Card, CardImg, CardText, CardBody,CardTitle,Spinner} from 'reactstrap';

import './Cards.css';
import recoveredImg from '../images/recovered.png';
import DeathImg from '../images/deaths.png';
import InfectedImg from '../images/infected.jpg';
import CountUp from 'react-countup';

const Cards = ({data:{confirmed, recovered, deaths, lastUpdate}}) => {
    if(!confirmed)
    {
        return <Spinner animation="border"/>;
    }
    return(
        <div className='container' id="cardContainer">
        <div className="row justify-content-center">
        <Card id="infected" className="col-xs-12 col-sm-3 card-style" height="auto">
            <CardBody>
                    <CardTitle className="h1 font-weight-bolder">Infected</CardTitle>
                    <div className="d-none d-sm-block">
                    <CardImg src={InfectedImg} />
                    </div>
                    <CardText >
                        <big><b>
                        <CountUp
                            start={0}
                            end={confirmed.value}
                            duration={2}
                            separator=","
                            />
                           </b> </big>
                    </CardText>
                   <CardText className="font-italic">{new Date(lastUpdate).toDateString()}</CardText>
                   <div className="d-none d-lg-block">
                    <CardText>Number of active cases of COVID-19</CardText></div>
                </CardBody>
        </Card>

        <Card id="recovered" className="col-xs-12 col-sm-3 offset-sm-1 card-style" height="auto">
            <CardBody>
                    <CardTitle className="h1 font-weight-bolder">Recovered</CardTitle>
                    <div className="d-none d-sm-block">
                    <CardImg src={recoveredImg} /></div>
                    
                    <CardText>
                    <big><b>
                        <CountUp
                            start={0}
                            end={recovered.value}
                            duration={2}
                            separator=","
                            />
                            </b></big>
                    </CardText>
                    
                   <CardText className="font-italic">{new Date(lastUpdate).toDateString()}</CardText>
                   <div className="d-none d-lg-block">
                    <CardText>Number of recoveries from COVID-19</CardText></div>
                </CardBody>
        </Card>
 
        <Card id="deaths" className="col-xs-12 col-sm-3 offset-sm-1 card-style" height="auto">
            <CardBody>
                    <CardTitle className="h1 font-weight-bolder" >Deaths</CardTitle>
                    <div className="d-none d-sm-block">
                    <CardImg width="100%" src={DeathImg} /></div>
                    
                    <CardText>
                    <big><b>
                        <CountUp
                            start={0}
                            end={deaths.value}
                            duration={2}
                            separator=","
                            />
                            </b></big>
                    </CardText>
                    
                   <CardText className="font-italic">{new Date(lastUpdate).toDateString()}</CardText>
                   <div className="d-none d-lg-block">
                    <CardText>Number of deaths from COVID-19</CardText></div>
                </CardBody>
        </Card>
        </div>
        </div>
    )
}
export default Cards;
